/*----------------------------------
2 - Header
----------------------------------*/

.header {
    margin: 0 0 27px;
}

.header:after {
    display: block;
    clear: both;
    content: "";
}

.header_top {
    background: #f8fafc;
    border-bottom: 1px solid #e0e4f6;
}

.contactinfo {
    font-size: 13px;
    float: left;
}

.contactinfo li {
    padding: 10px 15px 0;
    color: #373d54;
}

.contactinfo li * {
    line-height: 1;
}

.contactinfo li:first-child {
    margin-left: -15px;
}

.contactinfo li .fa {
    margin-right: 3px;
    margin-bottom: 2px;
    vertical-align: middle;
    color: #5f6680;
}

.social-icons {
    float: right;
    text-align: right;
    margin: 0;
}

.social-icons li a {
    border: 0 none;
    border-radius: 0;
    color: #5f6680;
    padding: 0px;
}

.social-icons li {
    display: inline-block;
}

.social-icons li a i {
    padding: 11px 15px;
    margin-bottom: -1px;
    transition: all 0.5s ease 0s;
    -moz-transition: all 0.5s ease 0s;
    -webkit-transition: all 0.5s ease 0s;
    -o-transition: all 0.5s ease 0s;
}

.social-icons li a i:hover {
    color: #fff;
    transition: all 0.5s ease 0s;
    -moz-transition: all 0.5s ease 0s;
    -webkit-transition: all 0.5s ease 0s;
    -o-transition: all 0.5s ease 0s;
}

.social-icons .fa-facebook:hover {
    background: #0083C9;
}

.social-icons .fa-twitter:hover {
    background: #5BBCEC;
}

.social-icons .fa-linkedin:hover {
    background: #FF4518;
}

.social-icons .fa-dribbble:hover {
    background: #90C9DC;
}

.social-icons .fa-google-plus:hover {
    background: #CE3C2D;
}

.social-icons .fa-instagram:hover {
    background: #b4307d;
}

.social-icons .fa-vk:hover {
    background: #4c75a3;
}

.header-middle {
    padding: 25px 0;
    text-align: justify;
}

.header-middle .header-middle-cont {
    font-size: 0;
}

.header-middle .header-middle-cont>* {
    font-size: 15px;
}

.header-middle .header-middle-cont:after {
    content: '';
    display: inline-block;
    width: 100%;
}

.toplogo {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
}

.toplogo a {
    display: block;
}

.toplogo img {
    display: block;
    max-height: 55px;
    max-width: 400px;
    width: auto;
    height: auto;
}

.shop-menu {
    
    text-align: left;
    display: inline-block;
    vertical-align: middle;
}

.shop-menu ul li {
    display: inline-block;
    padding-left: 15px;
    padding-right: 15px;
}

.shop-menu ul li:last-child {
    padding-right: 0;
}

.shop-menu ul li a {
    background: #FFFFFF;
    font-size: 14px;
    padding: 2px 0;
    line-height: 1;
    color: #373d54;
    display: inline-block;
}

.shop-menu ul li * {
    line-height: 1;
}

.shop-menu ul li a:hover {
    color: #373d54;
    opacity: 1 !important;
    background: transparent;
}

.shop-menu ul li a i {
    margin-right: 2px;
    color: #5f6680;
    padding: 0 1px;
    display: inline-block;
}

.shop-menu ul li a b {
    font-weight: 400;
}

.shop-menu ul li a .shop-menu-ttl {
    display: inline-block;
    border-bottom: 1px solid transparent;
    transition: all 0.2s;
    line-height: 1;
}

.shop-menu ul li a:hover .shop-menu-ttl {
    border-color: #5f6680;
}

.shop-menu ul li.topauth a:first-child {
    margin: 0;
    padding: 2px 12px 2px 0;
    background: url(../img/sep1.png) no-repeat right center;
}


