/*----------------------------------
3 - Topmenu
----------------------------------*/

.topmenu {
    position: relative;
    padding: 0 41px 0 203px;
    font-size: 14px;
}

.topmenu:after {
    clear: both;
    content: '';
    display: block;
}

.topmenu .topcatalog {
    text-align: left;
    position: absolute;
    top: 0;
    left: 0;
    width: 203px;
    z-index: 3;
}

.topcatalog-show .topmenu .topcatalog {
    z-index: 11;
}

.topmenu .topcatalog .topcatalog-btn {
    position: relative;
    color: #373d54;
    text-transform: uppercase;
    border: 1px solid #e0e4f6;
    display: block;
    padding: 11px 10px 11px 38px;
    font-weight: 500;
    background: #eff1fa url(../img/bar.png) no-repeat 14px 15px;
}

.topmenu .topcatalog .topcatalog-btn:after {
    position: absolute;
    top: 50%;
    right: 10px;
    content: '\f107';
    display: block;
    line-height: 1;
    margin: -8px 0 0;
    font-family: FontAwesome;
}

.topmenu .topcatalog:after {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    height: 5px;
    content: '';
    display: block;
}

.topcatalog .topcatalog-list {
    position: absolute;
    top: 100%;
    background: #fff;
    border-top: 1px solid #e0e4f6;
    border-left: 1px solid #e0e4f6;
    border-right: 1px solid #e0e4f6;
    text-align: left;
    margin: 4px 0 0;
    transition: all 0.3s ease;
    -webkit-transform: translate3d(0, 8px, 0);
    -moz-transform: translate3d(0, 8px, 0);
    -ms-transform: translate3d(0, 8px, 0);
    -o-transform: translate3d(0, 8px, 0);
    transform: translate3d(0, 8px, 0);
    visibility: hidden;
    opacity: 0;
    width: 0;
    overflow: hidden;
}

.topcatalog:hover .topcatalog-list {
    overflow: initial;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
    width: auto;
}

.topcatalog-list li {
    position: relative;
    line-height: 120%;
}

.topcatalog-list li a {
    color: #373d54;
    font-size: 14px;
    line-height: 120%;
    border-bottom: 1px solid #e0e4f6;
    display: block;
    padding: 11px 25px 11px 13px;
    transition: all 0.2s;
    background: #f8fafc;
    position: relative;
    width: 201px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.topcatalog-list li:hover>a {
    background: #fff;
}

.topcatalog-list li:after {
    content: '';
    background: #373d54;
    position: absolute;
    top: -1px;
    bottom: -1px;
    left: -1px;
    z-index: 3;
    width: 3px;
    display: none;
}

.topcatalog-list li:hover:after {
    display: block;
}

.topcatalog-list li .fa {
    position: absolute;
    right: 14px;
    line-height: 100%;
    top: 50%;
    margin: -1px 0 0;
    line-height: 100%;
    -ms-transform: translate(0, -50%);
    -o-transform: translate(0, -50%);
    -moz-transform: translate(0, -50%);
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
    transition: all 0.2s;
}

.topcatalog-list li ul {
    position: absolute;
    left: 100%;
    top: -1px;
    background: #fff;
    border: 1px solid #e0e4f6;
    transition: all 0.3s ease;
    -webkit-transform: translate3d(0, 8px, 0);
    -moz-transform: translate3d(0, 8px, 0);
    -ms-transform: translate3d(0, 8px, 0);
    -o-transform: translate3d(0, 8px, 0);
    transform: translate3d(0, 8px, 0);
    visibility: hidden;
    opacity: 0;
    width: 0;
    overflow: hidden;
}

.topcatalog-list li:hover>ul {
    overflow: initial;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
    width: auto;
}

.topcatalog-list li ul>li:last-child>a {
    border-bottom: none;
}

.topmenu .mainmenu-btn {
    position: relative;
    color: #373d54;
    text-transform: uppercase;
    border: 1px solid #e0e4f6;
    border-right: none;
    width: 100%;
    padding: 11px 10px 11px 43px;
    font-weight: 500;
    text-align: left;
    background: #eff1fa url(../img/bar.png) no-repeat 18px 10px;
    display: none;
}

.topmenu .mainmenu-btn:after {
    position: absolute;
    top: 50%;
    right: 15px;
    content: '\f107';
    display: block;
    line-height: 1;
    margin: -8px 0 0;
    font-family: FontAwesome;
}

.topmenu .mainmenu {
    border-top: 1px solid #e0e4f6;
    border-bottom: 1px solid #e0e4f6;
    background: #f8fafc;
    padding: 0 24px 0 24px;
}

.topmenu .mainmenu:after {
    clear: both;
    content: '';
    display: block;
}

.topmenu .mainmenu .fa {
    display: none;
}

.topmenu .mainmenu>li {
    float: left;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
    margin: 0 0 0 0;
    position: relative;
}

.topmenu .mainmenu>li>a {
    color: #373d54;
    display: block;
    transition: all 0.2s;
    padding: 11px 19px;
}

.topmenu .mainmenu>li.menu-item-has-children>a {
    padding-right: 30px;
}

.topmenu .mainmenu>li>a:hover,
.topmenu .mainmenu>li>a.active {
    color: #3a89cf;
}

.topmenu .mainmenu>li.menu-item-has-children>a:after {
    position: absolute;
    font-family: FontAwesome;
    content: '\f107';
    display: block;
    top: 50%;
    font-size: 14px;
    right: 13px;
    line-height: 100%;
    margin: -8px 0 0 0;
}

.topmenu .mainmenu>li>a>.fa {
    margin-left: 4px;
}

.topmenu .mainmenu>li:after {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    height: 5px;
    content: '';
    display: block;
}

.topmenu .mainmenu>li>.sub-menu {
    position: absolute;
    top: 100%;
    background: #fff;
    border-top: 1px solid #e0e4f6;
    border-left: 1px solid #e0e4f6;
    border-right: 1px solid #e0e4f6;
    text-align: left;
    margin: 5px 0 0;
    transition: all 0.3s ease;
    -webkit-transform: translate3d(0, 8px, 0);
    -moz-transform: translate3d(0, 8px, 0);
    -ms-transform: translate3d(0, 8px, 0);
    -o-transform: translate3d(0, 8px, 0);
    transform: translate3d(0, 8px, 0);
    visibility: hidden;
    opacity: 0;
    z-index: 4;
    width: 0;
}

.topmenu .mainmenu>li:hover>.sub-menu {
    width: auto;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
}

.topmenu .mainmenu>li>.sub-menu li {
    position: relative;
    line-height: 120%;
}

.topmenu .mainmenu>li>.sub-menu li a {
    text-transform: none;
    font-weight: 400;
    color: #373d54;
    font-size: 14px;
    line-height: 120%;
    border-bottom: 1px solid #e0e4f6;
    display: block;
    padding: 11px 25px 11px 13px;
    transition: all 0.2s;
    background: #f8fafc;
    position: relative;
    width: 201px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.topmenu .mainmenu>li>.sub-menu li a.active {
    color: #3a89cf;
}

.topmenu .mainmenu>li>.sub-menu li:hover>a {
    background: #fff;
}

.topmenu .mainmenu>li>.sub-menu li.menu-item-has-children>a:after {
    position: absolute;
    font-family: FontAwesome;
    content: '\f105';
    display: block;
    top: 50%;
    font-size: 14px;
    right: 14px;
    line-height: 100%;
    margin: -8px 0 0 0;
}

.topmenu .mainmenu>li>.sub-menu li:after {
    content: '';
    background: #373d54;
    position: absolute;
    top: -1px;
    bottom: -1px;
    left: -1px;
    z-index: 3;
    width: 3px;
    display: none;
}

.topmenu .mainmenu>li>.sub-menu li:hover:after {
    display: block;
}

.topmenu .mainmenu>li>.sub-menu li ul {
    position: absolute;
    left: 100%;
    top: -1px;
    background: #fff;
    border: 1px solid #e0e4f6;
    border-bottom: none;
    transition: all 0.3s ease;
    -webkit-transform: translate3d(0, 8px, 0);
    -moz-transform: translate3d(0, 8px, 0);
    -ms-transform: translate3d(0, 8px, 0);
    -o-transform: translate3d(0, 8px, 0);
    transform: translate3d(0, 8px, 0);
    visibility: hidden;
    opacity: 0;
    width: 0;
}

.topmenu .mainmenu>li>.sub-menu li:hover>ul {
    width: auto;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
}


