.shop-menu .topsearch {
    position: relative;
    top: 0;
    right: 0;
}

.topsearch .topsearch-btn {
    height: 42px;
    line-height: 38px;
    width: 39px;
    text-align: center;
    border: 0.25px solid #253060;
    border-radius: 38%;
    display: block;
    background: #f10606;
    position: relative;
}

.topsearch .topsearch-btn:hover {
    color: #3a89cf;
}

.topsearch .topsearch-btn .fa {
    display: inline-flex;
    padding: 1px;
}

.search-show .topsearch .topsearch-btn .fa:before {
    content: '\f00d';
}

.topsearch .topsearch-form {
    z-index: 3;
    position: absolute;
    top: 95%;
    right: 0;
    margin: 5px 0 0;
