4 - Stylization
----------------------------------*/

.maincont {
    margin-bottom: 50px;
    padding-bottom: 50px;
}

.maincont:after {
    display: block;
    clear: both;
    content: "";
}

.stylization {
    line-height: 1.7;
    font-size: 14px;
    color: #616161;
}

.stylization:after {
    display: block;
    clear: both;
    content: '';
}

.stylization p {
    line-height: 1.7;
    margin: 0 0 30px;
}

.stylization h1,
.stylization h2,
.stylization h3,
.stylization h4,
.stylization h5,
.stylization h6 {
    color: #373d54;
    margin: 0 0 30px;
}

.stylization h1 {
    font-size: 35px;
}

.stylization h2 {
    font-size: 28px;
}

.stylization h3 {
    font-size: 24px;
}

.stylization h4 {
    font-size: 21px;
}

.stylization h5 {
    font-size: 18px;
}

.stylization h6 {
    font-size: 16px;
}

.stylization a {
    color: #3a89cf;
    text-decoration: none;
    transition: all 0.2s;
    border-bottom: 1px solid transparent;
}

.stylization a:hover {
    text-decoration: none;
    border-color: #3a89cf;
}

.stylization strong,
.stylization b {
    color: #373d54;
}

.stylization ul {
    margin: 0 0 30px 18px;
    padding: 0;
}

.stylization ul>li {
    list-style-image: url("../img/chk1.png");
}

.stylization li {
    padding: 0;
    line-height: 1.7;
    margin-bottom: 5px;
}

.stylization li>ul {
    margin: 0 0 0 18px;
}

.stylization ol {
    list-style-type: decimal;
    margin: 0 0 30px 18px;
    padding: 0;
}

.stylization ol li {
    position: relative;
}

.stylization li>ol {
    margin: 0 0 0 18px;
}

.stylization address {
    margin: 0 0 30px;
    line-height: 1.7;
}

.stylization code {
    background-color: #e8e8e8;
    padding: 2px 4px;
    color: #373d54;
    font-family: Inconsolata, monospace;
}

.stylization pre {
    background: #f8fafc;
    border: 1px solid #c4d8ec;
    font-size: 16px;
    line-height: 1.42857143;
    margin: 0 0 34px;
    max-width: 100%;
    overflow: auto;
